<template>
  <div class="group-name py-4">
    <h5 class="component__heading pt-0 pt-lg-2 px-3 px-lg-5">Create Group</h5>
    <div class="row">
      <div class="col-lg-9 col-xl-7">
        <BetForm class="px-3 px-lg-5">
          <form action="" @submit.prevent="createGroup" v-show="!authUser.group">
            <div class="form-group">
              <div class="input-group">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon"><i class="fa fa-users" aria-hidden="true"></i></span>
                </div>
                <input v-model="group.name" type="text" class="form-control" placeholder="Group Name" required>
              </div>
            </div>
            <div class="form-group">
              <textarea v-model="group.description" id="" cols="3" rows="2"
                        class="form-control" placeholder="Description" required>
              </textarea>
            </div>
            <div class="form-group">
              <div class="form-check">
                <input v-model="group.private" class="form-check-input" type="checkbox" value="" id="defaultCheck1">
                <label class="form-check-label" for="defaultCheck1">Private ?</label>
              </div>
            </div>
            <div class="form-group">
              <BetButton :disabled="!group.name && !group.description">Create Group</BetButton>
            </div>
          </form>
        </BetForm>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import {REGISTER_GROUP} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetForm from '../Utilities/BetForm'
  import BetButton from '../Utilities/BetButton'

  export default {
    name: 'CreateGroup',
    components: {BetForm, BetButton},
    props: ['authUser'],
    data () {
      return {
        group: {
          name: '',
          description: '',
          private: false
        }
      }
    },
    methods: {
      createGroup () {
        axios.post(REGISTER_GROUP, this.group, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
          if (response.data != null) {
            this.group = ''
            this.$noty.success('Group Created Successfully')
          }
          this.$store.dispatch('init_auth_user', {request: 'refresh'})
        }, (error) => {
          console.log('cannot create group', error)
        })
      }
    }
  }
</script>

